<template>
  <div class="input-range">
    <div class="numeric-wrapper">
      <label>от</label
      ><BaseNumeric
        class="custom-text-field"
        currency=""
        separator="space"
        v-bind:precision="0"
        v-model="currentValue[0]"
      ></BaseNumeric>
    </div>
    <div class="numeric-wrapper">
      <label>до</label
      ><BaseNumeric
        class="custom-text-field"
        currency=""
        separator="space"
        v-bind:precision="0"
        v-model="currentValue[1]"
      ></BaseNumeric>
    </div>
  </div>
</template>

<script>
import BaseNumeric from "./BaseNumeric";

export default {
  name: "BaseInputRange",
  components: {
    BaseNumeric
  },
  props: ["value"],
  data() {
    return {
      currentValue: [this.value[0], this.value[1]]
    };
  },
  watch: {
    value(newValue) {
      this.currentValue = [newValue[0], newValue[1]];
    },
    currentValue(newValue) {
      if (newValue[0] !== this.value[0] || newValue[1] !== this.value[1]) {
        this.$emit("input", newValue);
      }
    }
  }
};
</script>
