<template>
  <div class="loader">
    <a
      v-if="$store.state.finished && $store.state.cars_count > 0"
      @click="see_cars();"
      class="show-cars"
      >Посмотреть {{ $store.state.cars_count | automobilesString }}</a
    >
    <div v-if="!$store.state.finished" class="searching">
      <div class="lds-dual-ring"></div>
      Ищем автомобили
    </div>

    <span
      v-if="$store.state.finished && $store.state.cars_count == 0"
      class="not-found"
      >Автомобили не найдены</span
    >
  </div>
</template>

<script>
export default {
  name: "Loader",
  methods: {
    see_cars() {
      let queryString = this.getQuery();

      this.$emit("hide-popup");
      this.$router.push({ path: "/", query: queryString });
    }
  }
};
</script>

<style lang="scss">
