.loader {
  font-weight: 900;
  font-size: 13px;
  letter-spacing: 1px;
}

.searching,
.show-cars,
.not-found {
  display: flex;
  height: 30px;
  justify-content: center;
  align-items: center;
}

.show-cars {
  text-transform: uppercase;
  color: $blue;
}

.not-found {
  color: $not-found;
}

.searching {
  text-transform: uppercase;
  color: #d2d4d7;
}

.lds-dual-ring {
  display: inline-block;
  width: 30px;
  height: 30px;
  margin-right: 10px;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 24px;
  height: 24px;
  margin: 1px;
  border-radius: 50%;
  border: 3px solid $blue;
  border-color: $blue transparent $blue transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>
