export function automobilesString(num) {
  if (num % 10 === 0) return num + " автомобилей";

  let n = num % 100;

  if (n < 10 || n > 20) {
    if (n % 10 === 1) return num + " автомобиль";
    else if (n % 10 < 5) return num + " автомобиля";
  }
  return num + " автомобилей";
}

export function oneDecimal(num) {
  return Number(num).toFixed(1);
}
