<template>
  <div class="checkbox-group">
    <label v-for="option in options" class="custom-radio"
      >{{ option.label }}
      <input
        type="radio"
        v-bind:value="option.value"
        v-model="currentValue"
        v-on:input="$emit('input', $event.target.value);"
      />
      <span class="checkmark"></span>
    </label>
  </div>
</template>

<script>
export default {
  name: "BaseRadioGroup",
  props: ["options", "value"],
  data() {
    return {
      currentValue: this.value
    };
  },
  watch: {
    value(newValue) {
      this.currentValue = newValue;
    }
  }
};
</script>

