<template
  ><div class="base-slider row">
    <vue-slider
      class="slider"
      v-bind="sliderOptions"
      ref="slider"
      v-model="currentValue"
      v-bind:min="min"
      v-bind:max="max"
    ></vue-slider
    >{{ currentValue < max ? currentValue + "+" : currentValue }}
  </div>
</template>

<script>
import vueSlider from "vue-slider-component";

export default {
  name: "BaseSlider",
  components: {
    vueSlider
  },
  props: ["value", "min", "max"],
  data() {
    return {
      currentValue: this.value,
      sliderOptions: {
        width: 146,
        height: 3,
        dotSize: 16,
        lazy: true,
        sliderStyle: {
          backgroundColor: "#27c79a",
          "box-shadow": "none"
        },
        tooltip: "never",
        bgStyle: {
          backgroundColor: "#27c79a",
          "border-radius": "0"
        },
        processStyle: {
          backgroundColor: "#EFEFF4",
          "border-radius": "0"
        }
      }
    };
  },
  watch: {
    value(newValue) {
      this.currentValue = newValue;
    },
    currentValue(newValue) {
      this.$emit("input", newValue);
    }
  }
};
</script>

<style lang="scss" scoped>
.slider {
  margin: 3px 2px 0 -8px;
}
</style>
