<template>
  <div class="settings__component city-picker divider-right">
    <a
      ref="button"
      @click="toggleCityPicker();"
      class="settings__setting settings__setting-city flex-column"
    >
      <span class="settings__label">Город получения и возврата</span>
      <span class="settings__choice"
        >{{ pickup_city_name
        }}<span v-if="getFilters.pickup_city_id !== getFilters.dropoff_city_id">
          – {{ dropoff_city_name }}</span
        ></span
      >
    </a>
    <div class="settings__popup-outer" ref="popup">
      <a class="settings__button-close" @click="hide();">
        <svg
          width="14"
          height="14"
          viewBox="0 0 14 14"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fill-rule="evenodd"
            clip-rule="evenodd"
            d="M14 1.41L12.59 0L7 5.59L1.41 0L0 1.41L5.59 7L0 12.59L1.41 14L7 8.41L12.59 14L14 12.59L8.41 7L14 1.41Z"
          />
        </svg>
      </a>
      <div class="city-picker-inner">
        <h3>
          <a @click="setMode(true);" id="pick-up">Получение</a> и
          <a @click="setMode(false);" id="dropoff">возврат</a
          ><span
            v-if="getFilters.pickup_city_id === getFilters.dropoff_city_id"
            id="same-city"
            >Там же</span
          >
        </h3>
        <div id="city-picker__active-bar"></div>
        <div id="city-picker__cities">
          <a
            class="city-picker__city"
            @click="selectCity(city.id, city.name);"
            v-for="city in cities"
            :key="city.key"
            v-bind:class="classObject(city.id)"
          >
            {{ city.name }}
            <svg
              v-if="city.airport"
              width="14"
              height="15"
              viewBox="0 0 14 15"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M14 10.5V9L8.10526 5.25V1.125C8.10526 0.5025 7.61158 0 7 0C6.38842 0 5.89474 0.5025 5.89474 1.125V5.25L0 9V10.5L5.89474 8.625V12.75L4.42105 13.875V15L7 14.25L9.57895 15V13.875L8.10526 12.75V8.625L14 10.5Z"
              />
            </svg>
          </a>
        </div>
        <div class="city-picker__buttons">
          <Loader v-on:hide-popup="hide();"></Loader>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Loader from "./Loader";

export default {
  name: "CityPicker",
  data() {
    return {
      cities: [],
      pickup_city_name: "",
      dropoff_city_name: "",
      choosing_pick_up_city: true,
      choosing_drop_off_city: true,
      filters: {}
    };
  },
  components: {
    Loader
  },
  watch: {
    //populate cities as soon as general info is received
    general(newValue) {
      newValue.city.cities.small.forEach(column => {
        column.forEach(element => {
          this.cities.push(element);
          if (element.id === this.getFilters.pickup_city_id) {
            this.pickup_city_name = element.name;
          }
          if (element.id === this.getFilters.dropoff_city_id) {
            this.dropoff_city_name = element.name;
          }
        });
      });
    }
  },
  methods: {
    hide: function() {
      this.$refs.popup.style.maxHeight = "0px";
      this.$refs.button.classList.remove("settings__setting-selected");
      document.getElementById("mobile-loader").style.display = "none";
      document.querySelector(".calendar").classList.add("divider-right");
      document.body.classList.remove("mobile-noscroll");
    },
    show: function() {
      this.$refs.popup.style.maxHeight = "1500px";
      this.$refs.button.classList.add("settings__setting-selected");
      document.getElementById("mobile-loader").style.display = "flex";
      document.querySelector(".calendar").classList.remove("divider-right");
      document.body.classList.add("mobile-noscroll");
    },
    toggleCityPicker: function() {
      if (this.$refs.popup.style.maxHeight !== "1500px") this.show();
      else this.hide();
    },
    setMode: function(mode) {
      this.choosing_pick_up_city = mode;
      this.choosing_drop_off_city = !mode;
      //move the underline:
      const bar = document.getElementById("city-picker__active-bar");
      let target = this.choosing_pick_up_city
        ? document.getElementById("pick-up")
        : document.getElementById("dropoff");

      bar.style.left = target.offsetLeft + "px";
      bar.style.width = target.clientWidth + "px";
    },

    selectCity: function(id, name) {
      if (this.choosing_pick_up_city) {
        this.$store.commit("setFilters", { pickup_city_id: id });
        this.pickup_city_name = name;
      }

      if (this.choosing_drop_off_city) {
        this.$store.commit("setFilters", { dropoff_city_id: id });
        this.dropoff_city_name = name;
      }

      this.$store.commit("getCarsCount");
    },
    classObject(id) {
      if (this.choosing_pick_up_city) {
        if (id === this.getFilters.pickup_city_id) return "selected";
      } else {
        if (id === this.getFilters.dropoff_city_id) return "selected";
      }
    }
  },
  mounted() {
    //hide filter popup when clicking outside it:
    document.addEventListener("click", e => {
      if (
        this.$refs.popup.contains(e.target) ||
        this.$refs.button.contains(e.target)
      )
        return;

      if (this.$refs.popup.style.maxHeight === "1500px") this.hide();
    });
  }
};
</script>

<style scoped lang="scss">
@import "./citypicker.scss";
</style>
