<template>
  <div class="select-range-wrapper">
    <div class="select-range">
      <BaseSelect
        label="от"
        v-model="currentValue[0]"
        v-bind:options="options"
      ></BaseSelect>
      <BaseSelect
        label="до"
        v-model="currentValue[1]"
        v-bind:options="options"
      ></BaseSelect>
    </div>
    <span
      v-if="
        parseInt(currentValue[0] * 10) === parseInt(min * 10) &&
          parseInt(currentValue[1] * 10) === parseInt(max * 10)
      "
      class="any"
      >Любой</span
    >
  </div>
</template>

<script>
import BaseSelect from "./BaseSelect";

export default {
  name: "BaseSelectRange",
  components: {
    BaseSelect
  },
  props: ["value", "min", "max", "step"],
  data() {
    return {
      currentValue: this.value,
      options: []
    };
  },
  created() {
    for (let i = this.min; i < this.max; i += this.step) {
      let option = Math.round(i * 10) / 10;
      this.options.push(option);
    }

    if (!this.options.includes(this.max)) {
      this.options.push(this.max);
    }
  },
  watch: {
    value(newValue) {
      this.currentValue = newValue;
    },
    currentValue(newValue) {
      this.$emit("input", newValue);
    }
  }
};
</script>

<style lang="scss">
@import "./../../_vars";

.any {
  display: block;
  margin-top: 24px;
  margin-bottom: 19px;
  color: $main-color;
}
</style>
