<template>
  <div class="checkbox-group">
    <label v-for="option in options" class="custom-checkbox"
      >{{ option.label }}
      <input
        type="checkbox"
        v-bind:value="option.value"
        v-model="currentValue"
      />
      <span class="checkmark"
        ><svg
          width="14"
          height="11"
          viewBox="0 0 14 11"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fill-rule="evenodd"
            clip-rule="evenodd"
            d="M5 11L0 6L1.41 4.59L5 8.17L12.59 0.580002L14 2L5 11Z"
          />
        </svg>
      </span>
    </label>
  </div>
</template>

<script>
export default {
  name: "BaseCheckboxGroup",
  props: ["options", "value"],
  data() {
    return {
      currentValue: this.value
    };
  },
  watch: {
    currentValue(newValue) {
      this.$emit("input", newValue);
    },
    value(newValue) {
      this.currentValue = newValue;
    }
  }
};
</script>

