<template>
  <div class="settings__component additional-filters">
    <div v-if="hasSetFilters" class="reset-icon">
      <svg
        v-on:click.stop.prevent="reset();"
        width="20"
        height="20"
        viewBox="0 0 20 20"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          fill-rule="evenodd"
          clip-rule="evenodd"
          d="M10 0C4.47 0 0 4.47 0 10C0 15.53 4.47 20 10 20C15.53 20 20 15.53 20 10C20 4.47 15.53 0 10 0ZM15 13.59L13.59 15L10 11.41L6.41 15L5 13.59L8.59 10L5 6.41L6.41 5L10 8.59L13.59 5L15 6.41L11.41 10L15 13.59Z"
        />
      </svg>
    </div>
    <svg
      v-if="!hasSetFilters && !open"
      class="arrow"
      width="10"
      height="5"
      viewBox="0 0 10 5"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path fill-rule="evenodd" clip-rule="evenodd" d="M0 0L5 5L10 0H0Z" />
    </svg>
    <svg
      v-if="!hasSetFilters && open"
      class="arrow arrow-up"
      width="10"
      height="5"
      viewBox="0 0 10 5"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        v-if="!hasSetFilters && open"
        fill-rule="evenodd"
        clip-rule="evenodd"
        d="M0 5L5 0L10 5H0Z"
      />
    </svg>
    <a
      ref="button"
      @click="togglePopup();"
      class="settings__setting settings__setting-filters flex-column"
    >
      <span
        v-if="hasSetFilters && $store.state.finished === true"
        class="settings__label"
        >{{ $store.state.cars_count | automobilesString }} из 245</span
      >
      <span class="settings__choice">{{ filterString }}</span>
    </a>
    <div class="settings__popup-outer mobile-noscroll" ref="popup">
      <a class="settings__button-close" @click="hide();">
        <svg
          width="14"
          height="14"
          viewBox="0 0 14 14"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fill-rule="evenodd"
            clip-rule="evenodd"
            d="M14 1.41L12.59 0L7 5.59L1.41 0L0 1.41L5.59 7L0 12.59L1.41 14L7 8.41L12.59 14L14 12.59L8.41 7L14 1.41Z"
          />
        </svg>
      </a>
      <div class="additional-filters-inner">
        <div class="main">
          <h3>Основные параметры</h3>
          <div class="row row-main" v-for="row in filter.main">
            <div class="column" v-for="element in row">
              <h4>{{ element.label }}</h4>
              <BaseCheckboxGroup
                v-if="element.type === 'Checkbox'"
                v-bind:options="element.options"
                v-model="element.value"
              ></BaseCheckboxGroup>
              <BaseRadioGroup
                v-else-if="element.type === 'Radio'"
                v-bind:options="element.options"
                v-model="element.value"
              ></BaseRadioGroup>
              <BaseInputRange
                v-else-if="element.type === 'InputRange'"
                v-model="element.value"
              ></BaseInputRange>
              <BaseSlider
                v-else-if="element.type === 'Slider'"
                v-model="element.value"
                v-bind:min="element.min"
                v-bind:max="element.max"
              ></BaseSlider>
              <BaseSelectRange
                v-else-if="element.type === 'SelectRange'"
                v-model="element.value"
                v-bind:min="element.min"
                v-bind:max="element.max"
                v-bind:step="element.step"
              ></BaseSelectRange>
            </div>
          </div>
        </div>
        <transition name="slide">
          <div class="advanced" v-if="show_advanced">
            <hr />
            <h3>Дополнительные параметры</h3>
            <template v-for="(row, index) in filter.advanced">
              <template v-for="element in row">
                <h4>{{ element.label }}</h4>
                <div class="row row-advanced">
                  <div class="column" v-for="optionGroup in element.options">
                    <BaseCheckboxGroup
                      v-if="element.type === 'Checkbox'"
                      v-bind:options="optionGroup"
                      v-model="element.value"
                    ></BaseCheckboxGroup>
                  </div>
                </div>
              </template>
              <hr v-if="index < filter.advanced.length - 1" />
            </template>
          </div>
        </transition>
        <div class="row last">
          <a @click="show_advanced = !show_advanced;" class="all-params"
            >{{ show_advanced ? "Свернуть" : "Все параметры"
            }}<svg
              v-if="!show_advanced"
              width="10"
              height="5"
              viewBox="0 0 10 5"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M0 0L5 5L10 0H0Z"
              />
            </svg>
            <svg
              v-if="show_advanced"
              width="10"
              height="5"
              viewBox="0 0 10 5"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M0 5L5 0L10 5H0Z"
              />
            </svg>
          </a>

          <div class="reset-wrapper">
            <a
              v-if="hasSetFilters"
              class="reset"
              v-on:click.stop.prevent="reset();"
              ><svg
                width="20"
                height="20"
                viewBox="0 0 20 20"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fill-rule="evenodd"
                  clip-rule="evenodd"
                  d="M10 0C4.47 0 0 4.47 0 10C0 15.53 4.47 20 10 20C15.53 20 20 15.53 20 10C20 4.47 15.53 0 10 0ZM15 13.59L13.59 15L10 11.41L6.41 15L5 13.59L8.59 10L5 6.41L6.41 5L10 8.59L13.59 5L15 6.41L11.41 10L15 13.59Z"
                /></svg
              >Сбросить</a
            >
          </div>
          <div class="loader-buttons">
            <Loader v-on:hide-popup="hide();"></Loader>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BaseRadioGroup from "./base/BaseRadioGroup";
import BaseCheckboxGroup from "./base/BaseCheckboxGroup";
import BaseSlider from "./base/BaseSlider";
import BaseSelectRange from "./base/BaseSelectRange";
import BaseInputRange from "./base/BaseInputRange";
import Loader from "./Loader";

import _ from "lodash";

export default {
  name: "AdditionalFilters",
  data() {
    return {
      filter: {
        main: [],
        advanced: []
      },
      open: false,
      show_advanced: false,
      initialized: false,
      hasSetFilters: false
    };
  },
  components: {
    BaseRadioGroup,
    BaseCheckboxGroup,
    BaseInputRange,
    BaseSelectRange,
    BaseSlider,
    Loader
  },
  computed: {
    filterString() {
      if (!this.hasSetFilters) return "Расширенные фильтры";

      let result = "";

      this.filter.main.forEach(row => {
        row.forEach(element => {
          if (Array.isArray(element.options)) {
            if (Array.isArray(element.value)) {
              element.value.forEach(value => {
                let found = element.options.find(
                  o => parseInt(o.value) === parseInt(value)
                );
                if (found) result += found.label + ", ";
              });
            } else {
              let found = element.options.find(
                o => parseInt(o.value) === parseInt(element.value)
              );
              if (found) result += found.label + ", ";
            }
          }
        });
      });

      this.filter.advanced.forEach(row => {
        row.forEach(element => {
          if (Array.isArray(element.options)) {
            if (Array.isArray(element.value)) {
              element.value.forEach(value => {
                element.options.forEach(optionGroup => {
                  let found = optionGroup.find(
                    o => parseInt(o.value) === parseInt(value)
                  );
                  if (found) {
                    result += found.label + ", ";
                  }
                });
              });
            }
          }
        });
      });

      if (result.length > 2) result = result.substring(0, result.length - 2);

      //if (result.length > 23) {
      // result = result.substring(0, 21);
      //result = result + "...";
      //}
      return result;
    }
  },

  methods: {
    reset() {
      for (var k in this.filter) {
        this.filter[k].forEach(row => {
          row.forEach(element => {
            if (Array.isArray(element.defaultValue))
              element.value = element.defaultValue.slice();
            else element.value = element.defaultValue;
          });
        });
      }
    },
    togglePopup() {
      if (this.$refs.popup.style.maxHeight !== "2000px") this.show();
      else this.hide();
    },
    hide() {
      this.$refs.popup.style.maxHeight = "0";
      this.$refs.button.classList.remove("settings__setting-selected");
      document.getElementById("mobile-loader").style.display = "none";
      document.body.classList.remove("mobile-noscroll");
      this.$refs.popup.classList.add("mobile-noscroll");
      this.open = false;
    },
    show() {
      this.$refs.popup.style.maxHeight = "2000px";
      this.$refs.button.classList.add("settings__setting-selected");
      document.getElementById("mobile-loader").style.display = "flex";
      document.body.classList.add("mobile-noscroll");
      this.$refs.popup.classList.remove("mobile-noscroll");
      this.open = true;
    }
  },
  mounted() {
    document.addEventListener("click", e => {
      if (
        this.$refs.popup.contains(e.target) ||
        this.$refs.button.contains(e.target)
      )
        return;

      if (this.$refs.popup.style.maxHeight === "2000px") this.hide();
    });
  },
  watch: {
    general(newValue) {
      //populate filters as soon as general info is received
      newValue.filter.main.forEach(row => {
        this.filter.main.push(row);
      });

      this.$store.state.general.filter.advanced.forEach(row => {
        this.filter.advanced.push(row);
      });
    },
    filter: {
      handler: function(v) {
        if (!this.initialized) {
          this.initialized = true;
          return;
        }

        let filters = {};

        this.hasSetFilters = false;

        this.filter.main.concat(this.filter.advanced).forEach(row => {
          row.forEach(element => {
            if (Array.isArray(element.value)) {
              //console.log(element.value + " default: " + element.defaultValue);
              if (
                element.type === "InputRange" ||
                element.type === "SelectRange"
              ) {
                filters[element.name + "_min"] = element.value[0];
                filters[element.name + "_max"] = element.value[1];
              } else filters[element.name] = element.value;

              if (
                !_.isEqual(
                  _.sortBy(element.value),
                  _.sortBy(element.defaultValue)
                )
              )
                this.hasSetFilters = true;
            } else {
              filters[element.name] = element.value;

              if (parseInt(element.value) !== parseInt(element.defaultValue))
                this.hasSetFilters = true;
            }
          });
        });

        //console.log(filters);

        this.$store.commit("setFilters", filters);
        this.$store.commit("getCarsCount");
      },
      deep: true
    }
  }
};
</script>

<style scoped lang="scss">
@import "./additionalfilters.scss";

.slide-enter-active {
  transition: max-height 1.2s;
}

.slide-leave-active {
  transition: max-height 0.8s;
}

.slide-enter,
.slide-leave-to {
  max-height: 0px;
}
</style>
