// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import Vuex from "vuex";
import App from "./App";
import VueRouter from "vue-router";
import SmoothScrollbar from "vue-smooth-scrollbar";
import * as filters from "./util/filters";

// register global utility filters.
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key]);
});

Vue.use(Vuex);
Vue.use(SmoothScrollbar);

function extractDate(dateString) {
  // YYYY-MM-DD to Date

  const y = dateString.substring(0, 4);
  const m = dateString.substring(5, 7);
  const d = dateString.substring(8, 10);
  return new Date(y, m - 1, d);
}

function addParams(url, params) {
  for (var k in params) {
    if (k === "pickup_date" || k === "dropoff_date") {
      const y = params[k].getFullYear();
      const m = params[k].getMonth() + 1;
      const d = params[k].getDate();
      url +=
        "&" +
        k +
        "=" +
        "" +
        y +
        "-" +
        (m < 10 ? "0" : "") +
        m +
        "-" +
        (d < 10 ? "0" : "") +
        d;
    } else if (Array.isArray(params[k])) {
      params[k].forEach(element => {
        url += "&" + k + "[]=" + element;
      });
    } else url += "&" + k + "=" + params[k];
  }

  return url;
}

const store = new Vuex.Store({
  state: {
    general: {},
    filters: {},
    finished: false,
    cars_count: 0
  },
  mutations: {
    setGeneralInfo(state, info) {
      state.general = Object.assign({}, info);

      state.filters["pickup_city_id"] = state.filters["dropoff_city_id"] =
        state.general.city.default.id;
      state.filters["pickup_date"] = extractDate(
        state.general.calendar.default.saveFrom
      );
      state.filters["dropoff_date"] = extractDate(
        state.general.calendar.default.saveTo
      );

      state.general.filter.main
        .concat(state.general.filter.advanced)
        .forEach(row => {
          row.forEach(element => {
            if (Array.isArray(element.value)) {
              if (
                element.type === "InputRange" ||
                element.type === "SelectRange"
              ) {
                state.filters[element.name + "_min"] = element.value[0];
                state.filters[element.name + "_max"] = element.value[1];
              } else state.filters[element.name] = element.value;
            } else {
              state.filters[element.name] = element.value;
            }
          });
        });
      state.general_downloaded = true;
    },
    setFilters(state, filters) {
      for (var k in filters) {
        if (filters[k] != null) Vue.set(state.filters, k, filters[k]); //using Vue.set to make sure it's updated correctly
      }
    },
    clearFilters(state, clear) {
      clear.forEach(key => {
        //console.log(key);
        Vue.delete(state.filters, key);
      });
    },
    getCarsCount(state) {
      state.finished = false;
      let url =
        "https://beta.myrentacar.co/api/cars/count?key=car_to_rent&signature=72af0db4fabfb23b278cbf60bbb9907b";

      url = addParams(url, state.filters);
      //console.log("vuex: " + url);
      //const proxy_url = "https://cors-anywhere.herokuapp.com/";
      fetch(url)
        .then(response => response.json())
        .then(json => {
          state.cars_count = json.count;
          state.finished = true;
        });
    }
  }
});

Vue.use(VueRouter);
Vue.config.productionTip = false;

Vue.mixin({
  methods: {
    getQuery() {
      let result = {};
      for (var k in this.$store.state.filters) {
        if (k === "pickup_date" || k === "dropoff_date") {
          result[k] = this.formatDate(this.$store.state.filters[k]);
        } else if (Array.isArray(this.$store.state.filters[k]))
          result[k + "[]"] = this.$store.state.filters[k];
        else result[k] = this.$store.state.filters[k];
      }

      return result;
    },
    formatDate(date) {
      // Date to YYYY-MM-DD
      const y = date.getFullYear();
      const m = date.getMonth() + 1;
      const d = date.getDate();
      return (
        "" + y + "-" + (m < 10 ? "0" : "") + m + "-" + (d < 10 ? "0" : "") + d
      );
    },
    addParameters(url, params) {
      return addParams(url, params);
    },
    daysBetween: function(firstDate, secondDate) {
      return Math.max(
        1,
        Math.round((secondDate - firstDate) / (1000 * 60 * 60 * 24))
      );
    }
  },
  computed: {
    general() {
      return this.$store.state.general;
    },
    getFilters() {
      return this.$store.state.filters;
    },
    days() {
      return this.daysBetween(
        this.getFilters.pickup_date,
        this.getFilters.dropoff_date
      );
    }
  }
});

const router = new VueRouter({
  mode: "history",

  routes: [
    {
      path: "*",
      // Specify the component to be rendered for this route
      component: App,     
    }
  ]
});

/* eslint-disable no-new */
new Vue({
  router,
  el: "#app",
  components: { App },
  store,
  template: "<router-view><App/></router-view>"
});
