<template>
  <div class="custom-select__wrapper">
    <div
      v-on:click="showPopup();"
      class="custom-select"
      v-bind:options="options"
      ref="select"
    >
      {{ label }} {{ value
      }}<svg
        width="12"
        height="8"
        viewBox="0 0 12 8"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          fill-rule="evenodd"
          clip-rule="evenodd"
          d="M10.59 0.590088L6 5.17009L1.41 0.590088L0 2.00009L6 8.00009L12 2.00009L10.59 0.590088Z"
        />
      </svg>
    </div>
    <div class="custom-select__popup" ref="popup">
      <smooth-scrollbar
        class="smooth-scrollbar"
        v-bind:options="scrollbarOptions"
      >
        <ul>
          <li v-for="option in options" v-on:click="select(option);">
            <svg
              v-if="value == option"
              width="15"
              height="11"
              viewBox="0 0 15 11"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M4.76691 8.6786L1.21092 5.25802L0 6.41462L4.76691 11L15 1.1566L13.7976 0L4.76691 8.6786Z"
              />
            </svg>
            {{ option | oneDecimal }}
          </li>
        </ul>
      </smooth-scrollbar>
    </div>
  </div>
</template>

<script>
export default {
  name: "BaseSelect",
  props: ["options", "value", "label"],
  data() {
    return {
      scrollbarOptions: {
        alwaysShowTracks: true
      }
    };
  },
  methods: {
    select(value) {
      this.$emit("input", value);
      this.hidePopup();
    },
    hidePopup() {
      this.$refs.popup.style.opacity = 0;
      this.$refs.popup.style.pointerEvents = "none";
    },
    showPopup() {
      this.$refs.popup.style.opacity = 1;
      this.$refs.popup.style.pointerEvents = "all";
    }
  },
  mounted() {
    //hide popup when clicking outside it:
    document.addEventListener("click", e => {
      if (
        this.$refs.popup.contains(e.target) ||
        this.$refs.select.contains(e.target)
      )
        return;

      if (this.$refs.popup.style.opacity === "1") this.hidePopup();
    });
  }
};
</script>

