@import "./../../_inputs";

.custom-select__popup:hover .scrollbar-thumb {
  background: $gray;
}

.smooth-scrollbar .scrollbar-thumb {
  background: $light-gray;
  border-radius: 2.5px;
  width: 5px;
}

.smooth-scrollbar .scrollbar-track {
  background: transparent;
  width: 5px;
}

.custom-select__popup li svg {
  position: absolute;
  top: 4px;
  left: 15px;
  fill: $main-color;
  margin-right: 9px;
}
</style>
